import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import Solutions from './views/Solutions.vue'
import Coupons from './views/Coupons.vue'
import PriceComparison from './views/PriceComparison.vue' 
import RewardsCharity from './views/RewardsCharity.vue'
import Jobs from './views/Jobs.vue'
import Form from './views/Form.vue'
import Mobile from './views/Mobile.vue'
import Custom from './views/Custom.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/solutions/cashback',
      name: 'solutions',
      component: Solutions
    },
    {
      path: '/solutions/coupons',
      name: 'coupons',
      component: Coupons
    },
    {
      path: '/solutions/price&comparison',
      name: 'pricecomparison',
      component: PriceComparison
    },
    {
      path: '/solutions/rewards&charity',
      name: 'rewardscharity',
      component: RewardsCharity
    },
    {
      path: '/jobs',
      name: 'jobs',
      component: Jobs
    },
    {
      path: '/form',
      name: 'form',
      component: Form
    },
    {
      path: '/solutions/mobile',
      name: 'mobile',
      component: Mobile
    },
    {
      path: '/solutions/custom',
      name: 'custom',
      component: Custom
    }
  ]
})
