<template>
	<div>
		<h2>Contact Us</h2>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm slideUp">
  <el-form-item prop="name">
    <el-input v-model="ruleForm.name" placeholder="First Name"></el-input>
  </el-form-item>
    <el-form-item prop="surname">
    <el-input v-model="ruleForm.surname" placeholder="Last Name"></el-input>
  </el-form-item>
  <el-form-item prop="email">
    <el-input v-model="ruleForm.email" placeholder="Email"></el-input>
  </el-form-item>
  <el-form-item prop="phone">
    <el-input v-model.number="ruleForm.phone" placeholder="Work Phone"></el-input>
  </el-form-item>
    <el-form-item prop="company">
    <el-input v-model="ruleForm.company" placeholder="Company"></el-input>
  </el-form-item>
  <el-form-item>
    <el-select v-model="ruleForm.region" placeholder="Country">
    </el-select>
  </el-form-item>
  <el-form-item prop="type">
    <el-checkbox-group v-model="ruleForm.type">
    <div>
      <el-checkbox label="Send NDA" name="type"></el-checkbox>
    </div>
    <div>
      <el-checkbox label="I consent to having Besttoolbars collect my personal details." name="type"></el-checkbox>
    </div>
    </el-checkbox-group>
  </el-form-item>
  <el-form-item label="Project description" prop="desc">
    <el-input type="textarea" v-model="ruleForm.desc" placeholder="Project description"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">Talk to our team</el-button>
    <el-button @click="resetForm('ruleForm')">Reset</el-button>
  </el-form-item>
</el-form>
	</div>
</template>
<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          surname: '',
          email: '',
          phone: '',
          company: '',
          regions: '',
          delivery: false,
          type: [],
          desc: ''
        },
        rules: {
          name: [
            { required: true, message: 'Please input your name', trigger: 'blur' },
            { min: 3, max: 5, message: 'Length should be 3 to 5', trigger: 'blur' }
          ],
          surname: [
            { required: true, message: 'Please input your surname', trigger: 'blur' },
            { min: 3, max: 5, message: 'Length should be 3 to 5', trigger: 'blur' }
          ],
          email: [
            { required: true, message: 'Please input email address', trigger: 'blur' },
            { type: 'email', message: 'Please input correct email address', trigger: ['blur', 'change'] }
          ],
          phone: [
            { required: true, message: 'Please input phone number'},
            { type: 'number', message: 'Phone must be a number'}
          ],
          region: [
            { required: true, message: 'Please select Activity zone', trigger: 'change' }
          ],
          type: [
            { type: 'array', required: true, message: 'Please сonfirm your consent to the processing of personal data', trigger: 'change' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
  }
</script>